.wrapper{
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 10;
    background-color: rgba(0,0,0, 0.7);
}
.content{
    display: flex;
    padding: 50px;
    overflow-y: auto;
    min-width: 50vw;
    max-width: 80vw;
    max-height: 70vh;
    background-color: rgba(100,100,100, 0.2);
    border-radius: 10px;
    white-space: pre-line
}