@import '../../scss/variables.scss';
.wrapper{
    position: fixed;
    display: flex;
    flex-direction: column;
    align-items: center;
    top: 50%;
    right: 20px;
    transform: translateY(-50%);
    font-size: 35px;
    color: $primary-color;
    &::before{
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        left:0;
        top:0;
    }

    @media (max-width:560px) {
        position: absolute;
        right: 50%;
        top: 60%;
        transform: translateX(50%);
        flex-direction: row;
        gap: 20px
    }
    
    }
    a{
        outline:none;
        &:focus .social{
            color:$secondary-color;
            &::after{
                transform: scale3d(1,1,1);
                transition: transform 0.8s;
                transform-origin: bottom;   
            }
        }
        &:focus .socialCircle{
            color:$secondary-color;
            &::after{
            animation: fillCircle 0.8s ;
            clip-path: circle(55% at 50% 50%);
            }
        }
}
.social {
    position: relative;
    cursor: pointer;
    transition: color 0.8s;
    outline: none;

    &::after{
        content: '';
        position: absolute;
        width: 80%;
        height: 80%;
        background-color: #c2c2c2;
        top:15%;
        left: 2px;
        z-index: -1;
        transform: scale3d(1,0,1);
        transition: transform 0.4s;
        transform-origin: top;
    }

    &:hover::after{
        transform: scale3d(1,1,1);
        transition: transform 0.8s;
        transform-origin: bottom;
    }


    &:hover{
        color:$secondary-color
    }
}

.socialCircle {
    position: relative;
    cursor: pointer;
    transition: color 0.8s;

    &::after{
        content: '';
        position: absolute;
        width: 80%;
        height: 80%;
        background-color: #c2c2c2;
        top:10%;
        left: 2px;
        z-index: -1;
        clip-path: circle(0% at 50% 0%);
    }

    &:hover::after{
        animation: fillCircle 0.8s ;
        clip-path: circle(55% at 50% 50%);
    }
    &:not(:hover)::after { animation: unfillCircle 0.4s ; } 

    &:hover{
        color:$secondary-color
    }
}

@keyframes fillCircle {
    0%{ clip-path: circle(0% at 50% 100%);}
    100%{clip-path: circle(55.0% at 50% 50%);}
}

@keyframes unfillCircle {
    0%{ clip-path: circle(55.0% at 50% 50%);}
    100%{clip-path: circle(0% at 50% 0%);}
}


.cv{
    &__wrapper{
        position: fixed;
        top:0;
        z-index: 100;
        background-color: rgba($color: #000, $alpha: 0.8);
        height: 100vh;

        transition: 0.6s;
    }
  }

  :global {
    .react-pdf__Page__canvas {
       max-width: 100vw !important;
       max-height: 90vh !important;
    }
}

.buttons{
    display: flex;
}
.button{
    margin: 0 auto;
    text-align: center;
    position: relative;
    color: $color-text;
    padding: 0px 10px;
    cursor: pointer;
    font-size: 50px;
    margin-top: 10px;
    width: fit-content;
    transition: 0.3s;
    &:focus{
        outline: none;
    }
    &:hover svg path {
        stroke-dashoffset: 0;
      }
    &:focus svg path {
        stroke-dashoffset: 0;
      }
    svg{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 110%;
        height: 120%;
        z-index: -1;
        overflow: auto;

        path{
            fill: transparent;
            stroke-width: 10px;
            stroke: currentColor;
            stroke-dasharray :1212;
            stroke-dashoffset: 1212;
            transition: stroke-dashoffset 0.5s cubic-bezier(0.29, 0.68, 0.74, 1.02); 
        }
    }

    @media (max-width:380px) {
        font-size: 40px
    }
}